@import 'scss/vars.scss';

.viewModes {
  display: flex;
	margin-left: 2.84rem;

  > button {
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 50%;
    display: grid;
    justify-content: center;
    align-content: center;
    align-items: stretch;
    transition: all var(--transition-speed);

    @media screen and (max-width: 1102px) {
      width: 2.4rem;
      height: 2.4rem;
    }

    @media screen and (max-height: 725px) {
      width: 2.3rem;
      height: 2.3rem;
    }

    > div {
      background: var(--side-color);
      border: 0.5px solid #000000;
    }

    &:first-child > div {
      width: 1.6875rem;
      height: 0.9375rem;
    }

    &:nth-child(2) {
      grid-template: 0.5rem / 0.75rem 0.75rem;
      column-gap: 1px;
    }

    &:nth-child(3) {
      grid-template-columns: 1.1875rem 0.5rem;
      grid-auto-rows: minmax(0.3125rem, auto);
      gap: 1px;
      grid-template-areas: 'md sm2' 'md sm3' 'md sm4';

      > div:first-child {
        grid-area: md;
      }
      > div:nth-child(2) {
        grid-area: sm2;
      }
      > div:nth-child(3) {
        grid-area: sm3;
      }
      > div:nth-child(4) {
        grid-area: sm4;
      }
    }

    &:last-child {
      grid-template: 0.5rem 0.5rem / 0.8125rem 0.8125rem;
      gap: 1px;
    }

    &.active {
      background-color: white;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    }
  }
}
