@import 'scss/vars.scss';

.scoresTable {
  height: 6.4375rem;
  width: 16.875rem;
  margin: 0 0.25rem;
  display: flex;
  text-align: center;
  overflow-x: scroll;
  font-family: var(--font-comfortaa);
  font-weight: normal;
  height: 100%;

  > div {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    flex: 0 0 1.875rem;
    transition: all var(--transition-speed);
    border-bottom: 2px solid transparent;
    @media (hover: hover) {
      > span + span:hover {
				background-color: white;
				box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
      }
    }
    > span.hidden {
      visibility: hidden;
    }

    > span:first-child {
      font-weight: bold;
    }

    @media (hover: hover) {
      &:hover {
        background-color: rgba(255, 255, 255, 0.3);
        cursor: pointer;
        border-bottom: 2px solid var(--side-color);
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        font-weight: bold;
      }
    }

    &.active {
      background-color: white;
      cursor: pointer;
      border-bottom: 2px solid var(--side-color);
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      font-weight: bold;
    }
  }
}
